import { hover } from "@testing-library/user-event/dist/hover";
import axios from "axios";
import { useEffect,useState } from "react";
import styled from "styled-components";
import { API_KEY } from "../App";

const Container = styled.div`
display:flex;
flex-direction:row;
padding:20px 30px;
justify-content:center;
border-bottom: 1px solid red;
box-shadow: 0 8px 16px -8px  red;
`;

const Coverimage = styled.img`
object-fit:cover;
height:352px;
`;


const InfoColumn = styled.div`
display:flex;
flex-direction:column;
margin:20px;`;



const MovieName = styled.span`
 font-size:22px;
 font-weight:600;
 color:#c8c8c8;
 margin:15px 0;
 white-space:nowrap;
 text-transform:capitalize;
 text-overflow:ellipses;
 overflow:hidden;
`;

const MovieInfo = styled.div`
font-size:16px;
font-weight:500;
color:#c9c9c9;
margin:4px 0;
text-transform:capitalize;
text-overflow:ellipses;

& span{
    opacity:0.8;
};
`

const Close = styled.span`

background-color: rgb(210 11 11);
width: 50px;
color: #fcf9f9;
height: 20px;
padding: 5px 1px 3px 12px;
border-radius: 10px;
text-decoration: none;
cursor: pointer;
box-shadow: 0px 6px 28px #a7172ce3;
`;

const Wait = styled.div`
 color:white;

`





const MovieInfoComponent = (props) =>{
    const [movieInfo,setMovieInfo] = useState();
    const {selectedMovie} = props;
    useEffect(() => {axios.get(`https://www.omdbapi.com/?i=${selectedMovie}&apikey=${API_KEY}`).then((response) => setMovieInfo(response.data));}, [selectedMovie]);
 
return  <Container>
  {movieInfo?<>

    <Coverimage src={movieInfo?.Poster}/>
    <InfoColumn>
    <MovieName>{movieInfo?.Type} : {movieInfo?.Title} </MovieName>
    <MovieInfo>IMDB Rating: <span>  {movieInfo?.imdbRating}   </span></MovieInfo>
    <MovieInfo>Year:  {movieInfo?.Year}   </MovieInfo>
    <MovieInfo>Language:  {movieInfo?.Language}   </MovieInfo>
    <MovieInfo>Rated:  {movieInfo?.Rated}   </MovieInfo>
    <MovieInfo>Release Date:  {movieInfo?.Released}   </MovieInfo>
    <MovieInfo>Duration / Runtime:  {movieInfo?.Runtime}   </MovieInfo>
    <MovieInfo>Genre:  {movieInfo?.Genre}   </MovieInfo>
    <MovieInfo>Directed By :  {movieInfo?.Director}   </MovieInfo>
    <MovieInfo>Lead Cast:  {movieInfo?.Actors}   </MovieInfo>
    <MovieInfo>Plot:  {movieInfo?.Plot}   </MovieInfo>
    </InfoColumn>
    
    <Close  onClick={() => props.onMovieSelect()}>Close</Close>
    
  
  </>: <Wait>Please Wait While we load...</Wait>}
 
    
    </Container>;
 
};

export default MovieInfoComponent;