import axios from "axios";
import {useState} from "react";
import styled from "styled-components";
import MovieComponent from "./components/MovieComponent";
import MovieInfoComponent from "./components/MovieInfoComponent";

export const API_KEY = "390ab90e";

const Container = styled.div`
display:flex;
flex-direction:column;

& p{
    color:grey;
    margin-left:25%;
    margin-top:7%;
}
`;
    

const Header = styled.div`
display:flex;
flex-direction:row;
justify-content:space-between;
background-color:black;
height:50px;
color:white;
align-items:center;
padding:10px;
font-size:25px;
font-weight:bold;
box-shadow:0 3px 6px 0 #555
`

const AppName = styled.div`
display:flex;
flex-direction:row;
align-items:center;
`;

const MovieImage = styled.img`
width:130px;
height:48px;
margin:15px;
`;

const MovieApps = styled.div`

& img {
    
    width: 33px;
    margin: 5px;
    opacity: 0.9;
}
`;







const SearchBox = styled.div`
background-color:#e2e2e2 !important;
display:flex;
flex-direction:row;
padding:10px 10px;
border-radius:6px;
margin-left:20px;
width:50%;
background-color:white;
align-items:center;
`;

const SearchIcon = styled.img`
width:32px;
height:32px;
`;


const SearchInput = styled.input`
background-color:#e2e2e2;
color:black;
font-size:16px;
font-weight:bold;
border:none;
outline:none;
margin-left:15px;
`;

const MovieListContainer = styled.div`

display:flex;
flex-direction:row;
flex-wrap:wrap;
gap:24px;
padding:20px;
justify-content:space-evenly;
`;

const PlaceHolder = styled.img`
width:300px;
height:300px;
margin:40px;
opacity:90%;
margin-right:9%;
`;



function App(){
    const [searchQuery, updateSearchQuery] = useState();
    const [timeoutId, updateTimeoutId] = useState();
    const [movieList, updateMovieList] = useState();
    const [selectedMovie, onMovieSelect] = useState();

    const fetchData = async (searchString) =>{
       const response =await  axios.get(`https://www.omdbapi.com/?s=${searchString}&apikey=${API_KEY}`);

       console.log(response)
       updateMovieList(response.data.Search)

    };

const onTextChange = (event) =>{
    clearTimeout(timeoutId)
    updateSearchQuery(event.target.value);
   const timeout =  setTimeout(() => fetchData(event.target.value),500)
   updateTimeoutId(timeout)
};


    return (
<Container>
    <Header>
        <AppName> <MovieImage src="/logo777.png"  />
           
           </AppName>
           <MovieApps><a href="https://www.rottentomatoes.com/"><img src="/logo666.png"></img></a>
           <a href="https://www.metacritic.com/"><img src="/logo999.png"></img></a>
           </MovieApps>
    
           <SearchBox>
  <SearchIcon src="/search-icon.svg" />
  <SearchInput placeholder="Search for a movie" value={searchQuery}  onChange={onTextChange} />
           </SearchBox>
           </Header>
           {selectedMovie && (<MovieInfoComponent selectedMovie={selectedMovie}  onMovieSelect={onMovieSelect}/>)}
           <MovieListContainer>
            {movieList?.length? movieList.map((movie, index) => <MovieComponent key={index} movie={movie} onMovieSelect={onMovieSelect} />): ( <PlaceHolder src="/logo888.png"/>)}
           </MovieListContainer>
           <p className="note">We deliver the Official-team registered details under the movie certification sensor dept.</p>

</Container>
     
    )
}







export default App;